import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import { connect } from 'react-redux';
import { cardsFetchData } from './../actions/cards';
import Card from './Card'


class Cards extends Component {

  constructor(props) {
    super(props);
  }

  componentDidMount = () => {
    this.props.fetchData('/api/me/might-match');
  }

  render() {
    if (this.props.hasErrored ) {
        return <p>Sorry! There was an error loading the items</p>;
    }

    if (this.props.isLoading) {
        return <p>Loading…</p>;
    }

    return (
      <div className="Cards CardsContainer">
        {this.props.cards.map((user, index) =>
          <Link to={`/profile/${user.id}`} key={index}>
            <Card
              name={user.name}
              avatar={user.avatar.avatar.medium_avatar.url}
            />
          </Link>
        )}
      </div>

    );
  }
}


const mapStateToProps = (state) => {
  return {
    cards: state.cards,
    hasErrored: state.itemsHasErrored,
    isLoading: state.itemsIsLoading
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    fetchData: (url) => dispatch(cardsFetchData(url))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(Cards);



