import React, { Component } from 'react';


class Card extends Component {

  constructor(props) {
    super(props);
    // console.log(this.props)
    // const w = this.refs.card.offsetWidth
    // const h = this.refs.card.offsetHeight
    // console.log(w,h)
    this.state = {
      width: 0,
      height: 0,
      mouseX: 0,
      mouseY: 0
    }
  }

  mousePX() {
    return this.state.mouseX / this.state.width;
  }

  mousePY() {
    return this.state.mouseY / this.state.height;
  }

  onMouseMove = (e) => {
    let mouseX = e.pageX - this.refs.card.offsetLeft - e.target.offsetWidth/2;
    let mouseY = e.pageY - this.refs.card.offsetTop - e.target.offsetHeight/2;
    this.setState({
      mouseX:mouseX,
      mouseY:mouseY,
      width: this.refs.card.offsetWidth,
      height: this.refs.card.offsetHeight,
    })
  }

  mouseLeaveDelay(){
    return null
  }

  onMouseLeave = (e) => {
    this.mouseLeaveDelay = setTimeout(()=>{
      this.setState({
        mouseX:0,
        mouseY:0
      })
    }, 400);
  }

  cardBgTransform() {
    const tX = this.state.mouseX * -40;
    const tY = this.state.mouseY * -40;
    return {
      transform: `translateX(${tX}px) translateY(${tY}px)`
    }
  }


  render() {
    const {name, avatar, about} = this.props
    const rX = this.mousePX() * 10;
    const rY = this.mousePY() * -10;
    const tX = this.mousePX() * -15;
    const tY = this.mousePY() * -15;
    const styleCard = {
      transform: `rotateY(${rX}deg) rotateX(${rY}deg) rotateZ(1deg)`,
    };
    const styleBG = {
      transform: `translateX(${tX}px) translateY(${tY}px)`,
      backgroundImage: `url(${avatar})`
    };
    return (
      <div ref="card" className="CardWrap" onMouseMove={this.onMouseMove} onMouseLeave={this.onMouseLeave}>
        <div className="Card" style={styleCard}>
          <div className="CardBg" style={styleBG }></div>
          <div className="CardInfo flex items-end">
            <h1>{name}</h1>
          </div>
        </div>
      </div>
    );
  }
}


export default Card



